import React from 'react'
import '../assets/css/apply.css';
import {Toast, Button, WingBlank, InputItem, WhiteSpace, List } from 'antd-mobile';
import axios from 'axios'

class Apply extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            loading: false,
            username: name,
            phone: phone,
            isbind: isbind == 1 ? true : false
        }
    }

    nameChange(value){
        this.setState({
            username: value
        });
    }

    submit(){
        if(!this.state.loading) {
            Toast.loading('', 30);
            this.setState({
                loading: true
            });

            axios({
                method: 'post',
                url: '/maintenance/apply',
                data: {

                }
            }).then(function (response) {
                if (response.status == 200 && response.data.code == 0) {
                    this.setState({
                        loading: false,
                        isbind: true
                    });
                    Toast.success('', 3);
                }
                else {
                    this.setState({
                        loading: false
                    });
                    Toast.fail(response.data.message, 2);
                }
            }.bind(this)).catch(function (error) {
                this.setState({
                    loading: false
                });
                Toast.fail('', 2);
            }.bind(this));
        }
    }

    render() {
        const btn = ()=>{
            if(!this.state.isbind){
                return (
                    <Button className="btn" type="primary" loading={this.state.loading} onClick={this.submit.bind(this)}></Button>
                );
            }
            else{
                return (
                    <a href="/devices/add">
                        <Button className="btn" type="primary" loading={this.state.loading}></Button>
                    </a>
                );
            }
        }

        return (
            <div className="d-container">
                <List renderHeader={() => ''}>
                    <div className="scan-container">
                        <InputItem
                            value={this.state.username}
                            editable={false}
                            onChange={this.nameChange.bind(this)}
                        ></InputItem>
                    </div>
                    <div className="scan-container">
                        <InputItem
                            value={this.state.phone}
                            editable={false}
                            onChange={this.nameChange.bind(this)}
                        ></InputItem>
                    </div>
                </List>
                <WhiteSpace size="xl" />
                <WingBlank size="lg">
                    {btn()}
                </WingBlank>
                <WhiteSpace size="xl" />
            </div>
        );
    }
}

export default Apply;



